$large: "(min-width: 1080px)";

#root {
  background-color: #22565C;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

@font-face {
  font-family: 'Righteous-Regular';
  src: local('Righteous-Regular'),
  url('./assets/Righteous/Righteous-Regular.woff') format('woff') {} }

@font-face {
  font-family: 'Digital';
  src: local('Digital'),
  url('./assets/digital-7/Digital-7Mono.woff') format('woff') {} }

.app {
  color: white;
  width: 60%;
  max-width: 610px;
  height: 70%; }

.pomodoro-clock {
  height: 100%;
  background-color: #558888;
  border-radius: 60px;
  box-shadow: 10px 10px 9px 0px rgba(0,0,0,1); }


.pomodoro-clock__title {
  text-shadow: 4px 2px 0px darkgray; }

.pomodoro-clock__top-tab {
  display: flex;
  flex-direction: column;
  font-family: 'Righteous-Regular';
  height: 40%; }

.pomodoro-clock__bottom-tab {
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }


.pomodoro-clock__title {
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 5vw;
  margin-bottom: 7%;
  padding-top: 2%; }

.lengths__length-switch {
  display: flex;
  flex-direction: column; }

.lengths__length-switch__adjs {
  display:flex {} }

.lengths__length-switch__adjs__counter {
  margin-right: 4%;
  margin-left: 4%; }

.pomodoro-clock__controls {
  display: flex;
  width: 50%;
  justify-content: center;
  font-size: 5vw;
  height: 25%;
  align-items: center;
  text-shadow: 4px 2px 0px darkgray; }


.pomodoro-clock__controls__play-pause {
  margin-right: 10%;
  display: flex; }

.btn-pointer:hover {
  cursor: pointer; }


@media screen and #{($large)} {
  .pomodoro-clock__title {
    font-size: 4rem; } }

@media screen and #{($large)} {
  .pomodoro-clock__controls {
    font-size: 3rem; } }
