$large: "(min-width: 1080px)";

.display {
	width: 60%;
	height: 75%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	border: 8px solid #13353a;
	border-radius: 50px; }
.display__mode {
	margin-bottom: 8%; }

.display__mode__title {
	font-size: 8vw;
	text-shadow: 4px 3px 5px black; }

.display__counter__counter {
	font-family: 'Digital';
	font-size: 10vw;
	padding: 1%; }
.display__counter {
	text-shadow: 8px 6px 5px black; }

@media screen and #{($large)} {
	.display__mode__title {
		font-size: 4rem; } }


@media screen and #{($large)} {
	.display__counter__counter {
		font-size: 6rem; } }

